<template>
    <a-config-provider :locale="localeAnt === 'en' ? enUS : zhCN">
        <a-card :title="$t('project_header')">
            <template #extra>
                <a-radio-group v-model:value="localeAnt">
                    <a-radio-button key="en" :value="enUS.locale">English</a-radio-button>
                    <a-radio-button key="cn" :value="zhCN.locale">中文</a-radio-button>
                </a-radio-group>
            </template>
            <a-card-grid style="width: 20%; text-align: center">
                <div>{{ $t('project_desc') }}</div>
            </a-card-grid>
            <a-card-grid style="width: 50%;" :hoverable="false">
                <a-form ref="formRef" :model="formState" :rules="rules" :label-col="labelCol" :wrapper-col="wrapperCol">
                    <a-form-item ref="gender" :label="$t('form_sex')" name="gender">
                        <a-radio-group v-model:value="formState.gender">
                            <a-radio value="0">{{ $t('form_content_female') }}</a-radio>
                            <a-radio value="1">{{ $t('form_content_male') }}</a-radio>
                        </a-radio-group>
                    </a-form-item>
                    <a-form-item ref="age" :label="$t('form_age')" name="age">
                        <a-input-number v-model:value="formState.age" :min="0" :max="100"
                            :placeholder="$t('placeholder_range', { min: 0, max: 100 })" style="width: 100%;">
                        </a-input-number>
                    </a-form-item>
                    <a-form-item ref="BMI" :label="$t('form_BMI')" name="BMI">
                        <a-input-number v-model:value="formState.BMI" :min="15" :max="30"
                            :placeholder="$t('placeholder_range', { min: 15, max: 30 })" style="width: 100%;">
                            <template #addonAfter>
                                kg/m<sup>2</sup>
                            </template>
                        </a-input-number>
                    </a-form-item>
                    <a-form-item ref="nation" :label="$t('form_nation')" name="nation">
                        <a-radio-group v-model:value="formState.nation">
                            <a-radio value="0">{{ $t('form_content_han_nation') }}</a-radio>
                            <a-radio value="1">{{ $t('form_content_not_han_nation') }}</a-radio>
                        </a-radio-group>
                    </a-form-item>
                    <a-form-item ref="marrage" :label="$t('form_marraged')" name="marriage">
                        <a-radio-group v-model:value="formState.marriage">
                            <a-radio value="0">{{ $t('form_content_marraged') }}</a-radio>
                            <a-radio value="1">{{ $t('form_content_not_marraged') }}</a-radio>
                        </a-radio-group>
                    </a-form-item>
                    <a-form-item ref="education" :label="$t('form_education')" name="education">
                        <a-radio-group v-model:value="formState.education">
                            <a-radio value="0">{{ $t('form_content_education_0') }}</a-radio>
                            <a-radio value="1">{{ $t('form_content_education_1') }}</a-radio>
                        </a-radio-group>
                    </a-form-item>
                    <a-form-item ref="carrer" :label="$t('form_careers')" name="carrer">
                        <a-radio-group v-model:value="formState.carrer">
                            <a-radio value="0">{{ $t('form_content_carrer_0') }}</a-radio>
                            <a-radio value="1">{{ $t('form_content_carrer_1') }}</a-radio>
                        </a-radio-group>
                    </a-form-item>
                    <a-form-item :label="$t('form_active_power')" name="power">
                        <a-select v-model:value="formState.power" placeholder="">
                            <a-select-option value=0>轻度</a-select-option>
                            <a-select-option value=1>中度</a-select-option>
                            <a-select-option value=2>重度</a-select-option>
                        </a-select>
                    </a-form-item>
                    <a-form-item :wrapper-col="{ span: 14, offset: 4 }">
                        <a-button type="primary" @click="onSubmit" :loading="computing">{{ $t('form_content_submit')
                            }}</a-button>
                        <a-button style="margin-left: 10px" @click="resetForm" :loading="computing">{{
        $t('form_content_reset')
    }}</a-button>
                    </a-form-item>
                </a-form>
            </a-card-grid>
            <a-card-grid style="width: 30%;">
                <div v-if="!error">
                    <div style="text-align: center;" v-if="!gotresult">
                        <a-spin :indicator="indicator" :spinning="computing">
                            {{ $t('project_result_desc') }}
                        </a-spin>
                    </div>
                    <div style="text-align: center;" v-if="gotresult">
                        <div>{{ $t('project_result') }}</div>
                        <a-divider />
                        <a-progress type="circle" stroke-linecap="square" :percent="result"
                            :stroke-color="getResultCircleColor()" />
                    </div>
                </div>
                <div v-else>
                    <a-alert :description="$t('error_result')" type="error" />
                </div>
            </a-card-grid>
        </a-card>
    </a-config-provider>
</template>

<script lang="ts" setup>
import { reactive, ref, toRaw, watch, onMounted } from 'vue';
// import type { UnwrapRef } from 'vue';
import type { Rule } from 'ant-design-vue/es/form';
import enUS from 'ant-design-vue/es/locale/en_US';
import zhCN from 'ant-design-vue/es/locale/zh_CN';
import { LoadingOutlined } from '@ant-design/icons-vue';
import { h } from 'vue';
const indicator = h(LoadingOutlined, {
    style: {
        fontSize: '50px',
    },
    spin: true,
});
import { useI18n } from 'vue-i18n'; // 导入 useI18n
const { t } = useI18n(); // 获取 t 方法
const { locale } = useI18n();
const localeAnt = ref(enUS.locale);
watch(localeAnt, val => {
    locale.value = val;
    // rules.BMI[1].message = t('placeholder_range', { min: 15, max: 30 });
});
const computing = ref<boolean>(false);
const gotresult = ref<boolean>(false);
const error = ref<boolean>(false);
const result = ref<number>(0);

const formRef = ref();
const labelCol = { span: 5 };
const wrapperCol = { span: 13 };
const formState = reactive({
    gender: "",
    age: undefined,
    BMI: undefined,
    power: undefined,
    nation: "",
    marriage: "",
    education: "",
    carrer: ""
});
const rules: Record<string, Rule[]> = {
    gender: [
        { required: true, message: 'require', trigger: 'change' },
    ],
    nation: [
        { required: true, message: 'require', trigger: 'change' },
    ],
    age: [
        { required: true, message: 'require', trigger: 'change' },
    ],
    BMI: [
        { required: true, message: 'require', trigger: 'change' },
        { type: 'number', min: 15, max: 30, message: t('placeholder_range', { min: 15, max: 30 }), trigger: 'blur' },
    ],
    carrer: [
        { required: true, message: 'require', trigger: 'change' },
    ],
    education: [
        { required: true, message: 'require', trigger: 'change' },
    ],
    marriage: [
        { required: true, message: 'require', trigger: 'change' },
    ],
    power: [
        {
            required: true,
            message: 'Please select at least one activity type',
            trigger: 'change',
        },
    ],
};
const onSubmit = () => {
    computing.value = true;
    gotresult.value = false;
    error.value = false;
    // setTimeout(() => {
    //     gotresult.value = true;
    //     result.value = Math.floor(Math.random() * 100);
    //     computing.value = false;
    // }, 1000);

    formRef.value
        .validate()
        .then(async () => {
            let data = toRaw(formState);
            console.log(data);

            // const url = 'http://106.15.231.37:5000/compute';
            const url = 'https://studiotang.fun/api1/compute';
            // const url = 'http://localhost:5000/compute';


            const response = await fetch(url, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(data)
            });

            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }

            const r = await response.json();
            console.log(r);
            gotresult.value = true;
            result.value = (r) * 100;
            computing.value = false;
        })
        .catch((e: any) => {
            computing.value = false;
            error.value = true;
            console.log('error', e);
        });
};
const resetForm = () => {
    formRef.value.resetFields();
    gotresult.value = false;
};

const getResultCircleColor = () => {
    let from = "";
    let to = "";
    if (result.value > 0 && result.value <= 40) {
        from = "#2f8c00";
        to = "#2f8c00";
    } else if (result.value > 40 && result.value <= 70) {
        from = "#de9400";
        to = "#de9400";
    } else if (result.value > 70) {
        from = "#a70008";
        to = "#a70008";
    }

    return {
        "0%": from,
        "100%": to
    }
}


onMounted(() => {
    // console.log("aaa")
    console.log(t("project_header"))
});
</script>